
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Masker from 'vux/src/components/masker/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

export default {
components: {
ViewBox,
XHeader,
Masker,
Swipeout,
SwipeoutItem,
SwipeoutButton
},
data () {
return {
index: 0,
show: false,
dcontent: '操作提示',
status: false,
cardNo: '',
bankCardList: [{bankcardNo: '1111111111111111111'}]
}
},
mounted () {
this.initData()
},
computed: {
},
methods: {
onCancel () {
this.status = false
},
onConfirm () {
this.status = false
this.unbindCard(this.cardNo)
},
showDialog (cardNo) {
this.status = true
this.cardNo = cardNo
},
addBankCard () {
this.$router.push('/addCard')
},
unbindCard (cardNo) {
let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/unbindBankCard.do'
let data = {
action: 'init_request',
path: path,
params: {
userToken: self.$store.state.userInfo.token,
cardNo: cardNo
}
}
this.$vux.loading.show({
text: 'loading'
})
self.$store.dispatch('normalRequest', data).then(res => {
this.$vux.loading.hide()
if (res.response === 'success') {
this.initData()
} else {
this.$vux.toast.text(res.data)
}
})
},
initData () {
let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/getAppBankCardList.do'
let data = {
action: 'init_request',
path: path,
params: {
userToken: self.$store.state.userInfo.token
}
}
self.$store.dispatch('normalRequest', data).then(res => {
if (res.response === 'success') {
self.bankCardList = res.data
} else {
this.$vux.toast.text(res.data)
}
})
}
}
}



